.requests{
  display: block;
  position: relative;
  float: left;
  width: 100%;
  box-sizing: border-box;
  padding: 30px 0;
  .request{
    display: block;
    position: relative;
    float: left;
    width: 100%;
    box-sizing: border-box;
    margin:10px auto;
    border: 1px solid #eee;
    .request__header{
      display: block;
      position: relative;
      float: left;
      width: 100%;
      box-sizing: border-box;
      background-color: #f1f1f1;
      padding: 7px 35px;
      h2{
        position: relative;
        display: block;
        float: left;
        margin: 0;
        width: 80%;
        color: #777;
        text-align: left;
        font-size: 16px;
        font-family: $cb;
        font-style: italic;
        cursor: default;
        span{
          margin-left:10px;
          color: #323232;
          font-style: normal;
        }
      }
      .btn-toggle{
        position: relative;
        display: block;
        float: right;
        width: 15%;
        color: #1a7b9f;
        text-align: right;
        font-size: 14px;
        font-family: $cr;
        cursor: pointer;
        font-style: italic;
        .arrow{
          display: inline-block;
          margin-left: 10px;
          width: 18px;
          height: 15px;
          background-image: url(../img/icons/down.png);
          background-size: cover;
          background-repeat: no-repeat;
        }
      }
    }
    .request__toggle-block{
      display: none;
      position: relative;
      float: left;
      width: 100%;
      box-sizing: border-box;
      padding:20px 0;
      .date,.status,.price,.availability{
        width: calc(70%/4);
        display: block;
        position: relative;
        float: left;
        box-sizing: border-box;
        text-align: center;
      }
      .corp{
        width: 30%;
        display: block;
        position: relative;
        float: left;
        box-sizing: border-box;
        text-align: center;
      }
      .titles{
        display: block;
        position: relative;
        float: left;
        width: 100%;
        box-sizing: border-box;
        border-bottom: 1px solid #EEEEEE;
        .date,.status,.corp,.price,.availability{
          font-style: italic;
          color: #777;
          font-size: 13px;
          font-family:$cr;
        }
      }
      .row{
        display: block;
        position: relative;
        float: left;
        width: 100%;
        box-sizing: border-box;
        .date,.status,.price,.availability{
          color: #777;
          font-size: 13px;
          font-family:$cr;
          padding: 27px 0 7px 27px;
        }
        .corp{
          color: #777;
          font-size: 13px;
          font-family:$cr;
          padding: 7px 0 7px 27px;
          a{
            color: #ff6b00;
            text-decoration: underline;
          }
        }
        .price{
          .orange{
            background-color: #fa6400;
            color: white;
            border-radius: 3px;
            padding: 10px 20px;
            text-align: center;
            b{
              margin-left:5px;
            }
          }
        }
      }
    }
  }
  .rotated {
    transform: rotate(180deg);
    -ms-transform: rotate(180deg); /* IE 9 */
    -moz-transform: rotate(180deg); /* Firefox */
    -webkit-transform: rotate(180deg); /* Safari and Chrome */
    -o-transform: rotate(180deg); /* Opera */
  }
}