.map{
  display: block;
  position: relative;
  float: left;
  width: 100%;
  height: 679px;
  #search-map {
    display: block;
    position: relative;
    width: 100%;
    height: 100%;
    padding: 0;
    margin: 0;
  }
  &__search{
    display: block;
    position: absolute;
    bottom: 0;
    left: 35px;
    float: left;
    width: 280px;
    height: 660px;
    box-sizing: border-box;
    .head{
      display: block;
      position: relative;
      float: left;
      box-sizing: border-box;
      width: 100%;
      background-color: black;
      margin-bottom: 10px;
      padding: 10px 15px;
      span{
        color: #ff6b00;
        font-family: $cr;
        font-size: 30px;
        font-weight: 400;
        margin-right: 10px;
        display: block;
        float: left;
        position: relative;
        line-height:1em;
      }
      input{
        width: 69%;
        height: 30px;
        float: left;
        position: relative;
        background-color: white;
        border-radius: 8px;
        color: #818181;
        font-family: $cr;
        font-size: 14px;
        font-weight: 300;
        box-sizing: border-box;
        border: none;
        padding-left: 10px;
      }
      .btn-search{
        width: 40px;
        height: 30px;
        background-color: #ff6b00;
        border-radius: 8px;
        border: none;
        box-sizing: border-box;
        margin-left:10px;
        display: block;
        position: relative;
        float: left;
        span{
          display: block;
          background-image: url("../img/icons/search.png");
          background-repeat: no-repeat;
          background-size: cover;
          width: 16px;
          height: 16px;
          margin-left:5px;
        }
      }
    }
    .search-descr{
      display: block;
      position: relative;
      float: left;
      background-color: white;
      box-sizing: border-box;
      width: 100%;
      &__head{
        display: block;
        position: relative;
        float: left;
        background-color: white;
        width: 100%;
        box-sizing: border-box;
        border-bottom: 1px solid #e8e8e8;
        padding: 15px 20px;
        h2{
          width: 100%;
          color: #272727;
          font-family: $cr;
          font-size: 16px;
          margin:0;
        }
      }
      &__settings{
        display: block;
        position: relative;
        float: left;
        background-color: white;
        width: 100%;
        box-sizing: border-box;
        border-bottom: 1px solid #e8e8e8;
        padding: 10px 20px 0;
        color: #272727;
        font-family: $cr;
        font-size: 11px;
        &--check{
          display: block;
          float: left;
          width: 75%;
          position: relative;
          font-size: 11px !important;
          .business-options__item--check+.business-options__item--forcheck{
            font-size:11px !important;
            margin:7px 10px 0 0;
          }
        }
        .btn-filtr{
          display: block;
          position: relative;
          float: left;
          width: 25%;
          color: #ff6b00;
          font-family: $cr;
          font-size: 11px;
          text-align: right;
          margin:6px 0;
          span{
            background-image: url("../img/icons/filters/settings.png");
            width: 13px;
            height: 13px;
            display: block;
            position: relative;
            float: left;
          }

        }
      }
      &__icons{
        display: block;
        position: relative;
        float: left;
        width: 100%;
        box-sizing: border-box;
        padding: 10px 0;
        border-bottom: 1px solid #e8e8e8;
        label > input{ /* HIDE RADIO */
          visibility: hidden; /* Makes input not-clickable */
          position: absolute; /* Remove input from document flow */
        }
        label > input + img{ /* IMAGE STYLES */
          cursor:pointer;
          border:2px solid transparent;
          background-color: #000001;
        }
        label > input:checked + img{ /* (RADIO CHECKED) IMAGE STYLES */
          border:2px solid green;
          border-radius:50%;
          background-color: rgba(40, 237, 76, .8);
        }
        .icon{
          display: block;
          position: relative;
          float: left;
          width: 25%;
          box-sizing: border-box;
          color: #333;
          font-family: $cr;
          font-size: 10px;
          text-align: center;
        }
      }
      &__results{
        display: block;
        position: relative;
        float: left;
        width: 100%;
        box-sizing: border-box;
        padding:15px 20px;
        overflow-y: scroll;
        height: 320px;
        /*@include scrollbars(1em, red);*/
        /* Let's get this party started */
        &::-webkit-scrollbar {
          width: 0.3em;
        }
        /* Track */
        &::-webkit-scrollbar-track {
          -webkit-border-radius: 3px;
          border-radius: 3px;
        }
        /* Handle */
        &::-webkit-scrollbar-thumb {
          -webkit-border-radius: 3px;
          border-radius: 3px;
          background: #aeaeae;
          -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
        }
        &::-webkit-scrollbar-thumb:window-inactive {
          background: #aeaeae;
        }
        .result{
          display: block;
          position: relative;
          float: left;
          width: 100%;
          box-sizing: border-box;
          font-size: 12px;
          margin-bottom:20px;
          color: black;
          h3{
            width: 75%;
            position: relative;
            display: block;
            color: #ff6b00;
            font-family: $cr;
            font-size: 18px;
            float: left;
            margin:0 0 15px;
          }
          .logo{
            display: block;
            position: relative;
            float: right;
            box-sizing: border-box;
            width: 40px;
            height: 40px;
            border: 0.5px solid #000002;
            border-radius: 50%;
            overflow: hidden;
            img{
              width: 100%;
              object-fit: cover;
            }
          }
          span{
            color: #848282;
            margin-bottom:5px;
          }
          .address-map{
            color: #3b3b3b;
            font-size:12px;
            a{
              color: #ff6b00;
            }
          }
        }
      }
    }
    .search-filters{
      position: absolute;
      top: 59px;
      left: 283px;
      display: none;
      box-sizing: border-box;
      width: 280px;
      background-color: #fff;
      overflow-y: scroll;
      height: 503px;
      /* Let's get this party started */
      &::-webkit-scrollbar {
        width: 0.3em;
      }
      /* Track */
      &::-webkit-scrollbar-track {
        -webkit-border-radius: 3px;
        border-radius: 3px;
      }
      /* Handle */
      &::-webkit-scrollbar-thumb {
        -webkit-border-radius: 3px;
        border-radius: 3px;
        background: #aeaeae;
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.5);
      }
      &::-webkit-scrollbar-thumb:window-inactive {
        background: #aeaeae;
      }
      .type-car,.model-car,.services,.back{
        display: block;
        position: relative;
        float: left;
        width: 100%;
        box-sizing: border-box;
        padding:15px 17px;
        border-bottom: 1px solid #e8e8e8;
        h3{
          width: 100%;
          color: #333;
          font-family: $cb;
          font-size: 14px;
          margin:0;
        }
        span{
          font-family:$cr;
          font-size:12px;
          color: #fa6400;
          cursor: pointer;
        }
      }
      .type-car{
        .business-cars__type--check+.business-cars__type--forcheck .business-cars__type--checked{
          width: 15px;
          height: 14px;
          border-radius: 4px;
        }
        .business-cars__type--check+.business-cars__type--forcheck{
          padding-left: 40%;
        }
        .business-cars__type{
          margin-right:8px;
          img{
            width: 100%;
            height: 45px;
          }
        }
        .business-cars__type--head{
          color: #464646;
          font-family: $cb;
          font-size: 9px;
          margin:10px 0;
        }
      }
      .model-car{
        h3{
            margin-bottom:10px;
          }
        .business-options__services{
          width: 33%;
        }
        .business-options__item--check+.business-options__item--forcheck .business-options__item--checked{
          width: 15px;
          height: 14px;
        }
        .business-options__item--check+.business-options__item--forcheck{
          margin-top:12px;
        }
        .business-options__item--element span{
          line-height:1em;
          margin-left:5px;
          color: #1b1b1b;
          font-family: $cb;
          font-size: 12px;
        }
        .business-options__item{
          width: 100%;
        }
        .business-options__item--fields{
          width: 15px;
        }

      }
      .services{
        h3{
          margin-bottom:10px;
        }
        .business-options__services{
          width: 100%;
        }
        .business-options__services .business-options__item{

        }
        .business-options__item--fields{
          width: 15px;
          height: 14px;
          margin-right: 15px;
        }
        .business-options__item--check+.business-options__item--forcheck .business-options__item--checked{
          width: 15px;
          height: 14px;
        }
        .business-options__item--element{
          span{
            color: #1b1b1b;
            font-family: $cb;
            font-size: 12px;
            line-height: 15px;
          }
        }
      }
      .head-filtr{
        width: 100%;
        display: block;
        position: relative;
        float: left;
        box-sizing: border-box;
        background-color: #ff6b00;
        padding:13px 15px;
        height:48px;
        h3{
          color: #fffcf4;
          font-size:18px;
          margin:0;
          font-family: $cb;
        }

      }
    }
  }
}

